.App {
  text-align: center;
  background-color: #222;
  padding-top: 0.25px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 50px;
}

.header-logo img{
  margin-top: 10px;
  margin-left: 20%;
  height: 40px;
}

.App-footer {
  background-color: #222;
  height: 80px;
  padding-top: 50px;
  color: white;
}
.container {
  background-color: #f2f2f2;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 6px;
}

.language-selection p {
  text-align: right;
  margin-right: 10%;
  margin-top: 5px;
  padding-top: 5px;
}

.language-selection p a {
  padding-left: 10px;
  padding-right: 10px;
  font-size: large;
}

.language-selection p a:hover {
  background-color: lightgray;
  border-radius: 10;
}

.home-style {
  background-color: #222;
  padding: 20px;
  color: white;
  border-radius: 4px;
}

.home-style h2 {
  padding-bottom:20px;
}

.home-style img {
  width: 120px;
  margin-top: 5px;
  margin-right:5px;
  border-radius: 2px;
}

.home-upper-block {
  padding-top: 25px;
  font-size: large;
}

.home-lower-block {
  padding-top: 15px;
  padding-bottom: 25px;
}

.start-block {
  margin-top: 20px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.start-block p {
  padding-top: 2px;
  padding-bottom: 2px;
  margin: auto;
  width: 100px;
  background-color: lightgray;
  border-radius: 4px;
}

.black-button {
  color: #222;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 4px;
  padding-bottom: 4px;
}

.black-button a h5 {
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 1.5px;
  padding-bottom: 1.5px;
  font-size: large;
}


.blue-button {
  color: #0080ff;
}

.show-resultsbox {
  padding-top: 20px;
  padding-bottom: 10px;
  font-size: large;
}

.result-info h5 {
  margin-left: 1%;,
  margin-right: 1%;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



